}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-width: 900px;
}

/* 
COMPONENT: Navigation Bar Style
*/

nav {
  background-color: var(--black);
  color: var(--white);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 0 1rem;
}

/*Navbar links*/
nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  grid-gap: 2.5rem;
  gap: 2.5rem;
}

nav li {
  padding-left: 1rem;
  padding-right: 1rem;
}

nav a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
}

nav a:hover {
  -webkit-text-decoration: underline 0.15rem var(--brown);
          text-decoration: underline 0.15rem var(--brown);
  text-underline-offset: 0.3rem;
}

nav a:active {
  -webkit-text-decoration: underline 0.15rem var(--light-brown);
          text-decoration: underline 0.15rem var(--light-brown);
  text-underline-offset: 0.3rem;
}

/*Navbar Title and Logo*/
.site-title {
  font-size: 2rem;
  font-family: 'Aladin', cursive;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

nav img {
  height: 1.5rem;
  width: 1.2rem;
  margin: 1rem 0.7rem 0 0;
}


